import axios from "axios"

export function request(config){
	//Axios里自带Promise
	// return new Promise((resolve,reject) => {})
	// 1.创建axios的实例
	const instance = axios.create({  //Axios实例，请求设置
		baseURL:'http://152.136.185.210:7878/api/m5',
		timeout:5000
	})
	
	// 2.axios的拦截器
	instance.interceptors.request.use(config => {
		// console.log(config);
		//2.1拦截器的作用
		//1.比如config中的一些信息不符合服务器邀请
		// 2.比如每次发送请求时,都希望界面中显示一个请求的图标
		// 3.某些网络请求(比如登陆(token)),必须携带一些特殊信息
		return config  //拦截完返回出去，否则后面拿不到结果
	},err => {
		console.log(err);
	})
	
	// 2.2拦截响应
	instance.interceptors.response.use(res => {
		// console.log(res);
		return res.data  //拦截完返回出去，否则后面拿不到结果
	},err => {
		console.log(err);
	})
	
	
	//3.发送正在的网络请求
	// instance(config.baseConfig)
	// 	.then(res => {
	// 		config.success(res)
	// 	})
	// 	.catch(err => {
	// 		config.failure(err)
	// 	})
	
	return instance(config)
}