import Swiper from 'swiper/bundle';
import swiper2 from './swiper'
import swiperItem from './swiperItem'

export function iniSwiper(){
  const swiper1 = new Swiper('.swiper-container', {
    loop : true,
    autoplay:{
      delay:3000,
      disableOnInteraction: false,
    },
    initialSlide :0,		//轮播开始位置
    observer:true,			//轮播元素为内部动态添加必须加此 修改swiper自己或子元素时，自动初始化swiper
    observeParents:true,	//轮播元素为内部动态添加必须加此 修改swiper的父元素时，自动初始化swipe
    pagination: {
      el: '.swiper-pagination',
    },
  });
}
export {swiper2,swiperItem}